import React, { useEffect, useState } from 'react';
import { Col, Form, Row } from 'react-bootstrap';
import { useLocation, useNavigate } from 'react-router-dom';
import Type from '../Model/Type';

const formatDateToInputValue = (date) => {
    const year = String(date.getFullYear()).padStart(4, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
};

const validateAge = (naissance) => {
    if (!naissance) return false;
    const now = new Date();
    const naissanceDate = new Date(naissance);
    const age = now.getFullYear() - naissanceDate.getFullYear();
    const monthDiff = now.getMonth() - naissanceDate.getMonth();
    const dayDiff = now.getDate() - naissanceDate.getDate();
    return !(age < 18 || (age === 18 && (monthDiff < 0 || (monthDiff === 0 && dayDiff < 0))));
};

const InfoPersonel = ({ setText }) => {
    const [post, setPost] = useState([]);
    const [postSelected, setPostSelected] = useState(null);
    const [formValues, setFormValues] = useState({
        nom: '',
        prenom: '',
        naissance: new Date(),
        tel: '+261 ',
        adresse: '',
        cin: '',
        valide: new Date(),
        nationalite: 'Malagasy',
        genre: null,
        statut: null,
        enfant: 0,
        niveau: null,
    });

    const [validationErrors, setValidationErrors] = useState({
        validTel: true,
        validCIN: true,
        validNom: true,
        validPost: true,
        validPrenom: true,
        validNaissance: true,
        validAddress: true,
        validDate: true,
    });

    const navigate = useNavigate();
    const { search } = useLocation();

    const genres = [
        { id: 1, genre: 'Homme' },
        { id: 2, genre: 'Femme' },
        { id: 3, genre: 'Autre' },
    ];

    const statuts = [
        { id: 1, value: 'Célibataire' },
        { id: 2, value: 'Marié(e)' },
        { id: 3, value: 'Séparé(e)' },
        { id: 4, value: 'Veuf/veuve' },
        { id: 5, value: 'En union libre' },
    ];

    const niveaus = [
        { id: 1, value: '< Bac' },
        { id: 2, value: 'Bac +' },
        { id: 3, value: 'Licence' },
        { id: 4, value: 'Master I' },
        { id: 5, value: 'Master II' },
    ];

    useEffect(() => {
        setText('Information Personnelle');
        const queryParams = new URLSearchParams(search);

        if (queryParams.size === 0) {
            setFormValues(prev => ({
                ...prev,
                niveau: niveaus[0],
                statut: statuts[0],
                genre: genres[0],
            }));
        } else {
            setPostSelected(JSON.parse(decodeURIComponent(queryParams.get('type') || '{}')));
            setFormValues({
                nom: queryParams.get('nom') || '',
                prenom: queryParams.get('prenom') || '',
                naissance: new Date(queryParams.get('naissance')) || new Date(),
                tel: queryParams.get('tel') || '+261 ',
                adresse: queryParams.get('adresse') || '',
                cin: queryParams.get('cin') || '',
                valide: new Date(queryParams.get('valide')) || new Date(),
                nationalite: queryParams.get('nationalite') || 'Malagasy',
                genre: JSON.parse(decodeURIComponent(queryParams.get('genre') || '{}')) || null,
                statut: JSON.parse(decodeURIComponent(queryParams.get('statut') || '{}')) || null,
                enfant: parseInt(queryParams.get('enfant') || '0', 10),
                niveau: JSON.parse(decodeURIComponent(queryParams.get('niveau') || '{}')) || null,
            });
        }

        Type.getAllPostes()
            .then(res => {
                if (res.length > 0 && queryParams.size === 0) {
                    setPostSelected(res[0]);
                }
                setPost(res);
            })
            .catch(console.error);
    }, [search]);

    const handleChange = (e, field) => {
        setFormValues(prev => ({ ...prev, [field]: e.target.value }));
    };

    const handleDateChange = (e, field) => {
        const value = e.target.value;
        setFormValues(prev => ({ ...prev, [field]: new Date(value) }));
    };

    const handleTelChange = (e) => {
        let value = e.target.value.slice(1).replace(/\D/g, '');
        if (value.length > 12) value = value.slice(0, 12);
        const formatted = value.slice(3).replace(/(\d{2})(\d{2})(\d{3})(\d{2})/, '$1 $2 $3 $4');
        setFormValues(prev => ({ ...prev, tel: '+261 ' + formatted }));
    };

    const handleCinChange = (e) => {
        let value = e.target.value.replace(/\D/g, '').slice(0, 12);
        setFormValues(prev => ({ ...prev, cin: value.replace(/(.{3})/g, '$1 ').trim() }));
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const isValidNaissance = validateAge(formValues.naissance);

        const errors = {
            validTel: formValues.tel.length >= 15,
            validCIN: formValues.cin.length >= 14,
            validNom: formValues.nom.trim().length > 0,
            validPost: postSelected !== null,
            validPrenom: formValues.prenom.trim().length > 0,
            validNaissance: isValidNaissance,
            validAddress: formValues.adresse.trim().length > 0,
            validDate: !isNaN(formValues.valide.getTime()),
        };

        setValidationErrors(errors);
        const isValid = Object.values(errors).every(Boolean);

        if (!isValid) {
            return;
        }

        const queryParams = new URLSearchParams(search);

        queryParams.set('type', JSON.stringify({ idType: postSelected?.idType || -100, poste: postSelected?.poste || '' }));
        queryParams.set('nom', formValues.nom.trim());
        queryParams.set('prenom', formValues.prenom.trim());
        queryParams.set('naissance', formatDateToInputValue(formValues.naissance));
        queryParams.set('tel', formValues.tel);
        queryParams.set('adresse', formValues.adresse.trim());
        queryParams.set('cin', formValues.cin);
        queryParams.set('valide', formatDateToInputValue(formValues.valide));
        queryParams.set('nationalite', formValues.nationalite);
        queryParams.set('genre', JSON.stringify({ id: formValues.genre?.id || -100, value: formValues.genre?.genre || '' }));
        queryParams.set('statut', JSON.stringify({ id: formValues.statut?.id || -100, value: formValues.statut?.value || '' }));
        queryParams.set('enfant', formValues.enfant.toString());
        queryParams.set('niveau', JSON.stringify({ id: formValues.niveau?.id || -100, value: formValues.niveau?.value || '' }));

        navigate(`/inscription/rh-info?${queryParams.toString()}`);
    };

    return (
        <Form autoComplete="off" onSubmit={handleSubmit}>
            <Row className="mb-3">
                <Form.Group className="mb-3" as={Col} md="4" controlId="poste">
                    <Form.Label className="fw-bold">Poste</Form.Label>
                    <Form.Select
                        isInvalid={!validationErrors.validPost}
                        value={postSelected?.idType || ''}
                        onChange={(e) => setPostSelected(post.find(element => element.idType === parseInt(e.target.value, 10)))}
                        className="bg-transparent text-white border-dark-light"
                        required
                    >
                        {post.map(e => (
                            <option key={e.idType} className="text-dark" value={e.idType}>{e.poste}</option>
                        ))}
                    </Form.Select>
                    {!validationErrors.validPost && <Form.Control.Feedback type="invalid">Le poste est requis.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="nom">
                    <Form.Label className="fw-bold">Nom</Form.Label>
                    <Form.Control
                        type="text"
                        className="bg-transparent text-white border-dark-light"
                        value={formValues.nom}
                        onChange={(e) => handleChange(e, 'nom')}
                        isInvalid={!validationErrors.validNom}
                        placeholder="Nom"
                        required
                    />
                    {!validationErrors.validNom && <Form.Control.Feedback type="invalid">Le nom est requis.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="prenom">
                    <Form.Label className="fw-bold">Prénom</Form.Label>
                    <Form.Control
                        type="text"
                        className="bg-transparent text-white border-dark-light"
                        value={formValues.prenom}
                        onChange={(e) => handleChange(e, 'prenom')}
                        isInvalid={!validationErrors.validPrenom}
                        placeholder="Prénom"
                        required
                    />
                    {!validationErrors.validPrenom && <Form.Control.Feedback type="invalid">Le prénom est requis.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="naissance">
                    <Form.Label className="fw-bold">Date de naissance</Form.Label>
                    <Form.Control
                        type="date"
                        value={formatDateToInputValue(formValues.naissance)}
                        onChange={(e) => handleDateChange(e, 'naissance')}
                        isInvalid={!validationErrors.validNaissance}
                        required
                        className="bg-transparent text-white border-dark-light"
                    />
                    {!validationErrors.validNaissance && <Form.Control.Feedback type="invalid">Vous devez avoir au moins 18 ans.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="tel">
                    <Form.Label className="fw-bold">Téléphone</Form.Label>
                    <Form.Control
                        type="text"
                        value={formValues.tel}
                        onChange={handleTelChange}
                        isInvalid={!validationErrors.validTel}
                        placeholder="+261 00 00 000 00"
                        required
                        className="bg-transparent text-white border-dark-light"
                    />
                    {!validationErrors.validTel && <Form.Control.Feedback type="invalid">Le numéro de téléphone est invalide.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="adresse">
                    <Form.Label className="fw-bold">Adresse</Form.Label>
                    <Form.Control
                        type="text"
                        value={formValues.adresse}
                        onChange={(e) => handleChange(e, 'adresse')}
                        isInvalid={!validationErrors.validAddress}
                        placeholder="Adresse"
                        className="bg-transparent text-white border-dark-light"
                        required
                    />
                    {!validationErrors.validAddress && <Form.Control.Feedback type="invalid">L'adresse est requise.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="cin">
                    <Form.Label className="fw-bold">CIN</Form.Label>
                    <Form.Control
                        type="text"
                        value={formValues.cin}
                        onChange={handleCinChange}
                        isInvalid={!validationErrors.validCIN}
                        placeholder="CIN"
                        className="bg-transparent text-white border-dark-light"
                        required
                    />
                    {!validationErrors.validCIN && <Form.Control.Feedback type="invalid">Le CIN est invalide.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="valide">
                    <Form.Label className="fw-bold">Date de validité</Form.Label>
                    <Form.Control
                        type="date"
                        value={formatDateToInputValue(formValues.valide)}
                        onChange={(e) => handleDateChange(e, 'valide')}
                        isInvalid={!validationErrors.validDate}
                        className="bg-transparent text-white border-dark-light"
                        required
                    />
                    {!validationErrors.validDate && <Form.Control.Feedback type="invalid">La date de validité est requise.</Form.Control.Feedback>}
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="4" controlId="nationalite">
                    <Form.Label className="fw-bold">Nationalité</Form.Label>
                    <Form.Select value={formValues.nationalite} onChange={(e)=> handleChange(e, 'nationalite')} className="bg-transparent text-white border-dark-light" required>
                         <option className="text-dark" value={"Malagasy"}>Malagasy</option>
                         <option className="text-dark" value={"Etranger"}>Etranger</option>
                    </Form.Select>
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="3" controlId="genre">
                    <Form.Label className="fw-bold">Genre</Form.Label>
                    <Form.Select
                        value={formValues.genre?.id || ''}
                        onChange={(e) => setFormValues(prev => ({
                            ...prev,
                            genre: genres.find(g => g.id === parseInt(e.target.value, 10))
                        }))}
                        className="bg-transparent text-white border-dark-light"
                        required
                    >
                        {genres.map(g => (
                            <option key={g.id} className="text-dark" value={g.id}>{g.genre}</option>
                        ))}
                    </Form.Select>
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="3" controlId="statut">
                    <Form.Label className="fw-bold">Statut</Form.Label>
                    <Form.Select
                        value={formValues.statut?.id || ''}
                        onChange={(e) => setFormValues(prev => ({
                            ...prev,
                            statut: statuts.find(s => s.id === parseInt(e.target.value, 10))
                        }))}
                        className="bg-transparent text-white border-dark-light"
                        required
                    >
                        {statuts.map(s => (
                            <option key={s.id} className="text-dark" value={s.id}>{s.value}</option>
                        ))}
                    </Form.Select>
                </Form.Group>

                <Form.Group className="mb-3" as={Col} md="3" controlId="enfant">
                    <Form.Label className="fw-bold">Nombre d'enfants</Form.Label>
                    <Form.Control
                        type="number"
                        value={formValues.enfant}
                        className="bg-transparent text-white border-dark-light"
                        onChange={(e) => setFormValues(prev => ({ ...prev, enfant: parseInt(e.target.value, 10) || 0 }))}
                        placeholder="Nombre d'enfants"
                    />
                </Form.Group>
                <Form.Group className="mb-3" as={Col} md="3" controlId="niveau">
                    <Form.Label className="fw-bold">Niveau</Form.Label>
                    <Form.Select
                        value={formValues.niveau?.id || ''}
                        onChange={(e) => setFormValues(prev => ({
                            ...prev,
                            niveau: niveaus.find(n => n.id === parseInt(e.target.value, 10))
                        }))}
                        className="bg-transparent text-white border-dark-light"
                        required
                    >
                        {niveaus.map(n => (
                            <option key={n.id} className="text-dark" value={n.id}>{n.value}</option>
                        ))}
                    </Form.Select>
                </Form.Group>
            </Row>

            <Row>
                <Col md={6}>
                    <button type="button" onClick={()=>{navigate("/")}} className="btn btn-lg bg-aris text-white w-100">Login</button>
                </Col>
                <Col md={6}>
                    <button type="submit" className="btn btn-lg bg-blue text-white w-100">Suivant</button>
                </Col>
            </Row>
        </Form>
    );
};

export default InfoPersonel;
