import React, {useEffect, useState} from "react";
import {Col, Form, Row} from "react-bootstrap";
import {useLocation, useNavigate} from 'react-router-dom';

const InfoBanque = ({setText}) => {
    const { search } = useLocation();
    const navigate=useNavigate()
    const queryParams = new URLSearchParams(search);

    const [entre,setEntre]=useState(new Date())
    const [numCanps,setCnaps]=useState('')
    const [banque,setBanque]=useState(null)
    const [rib,setRib]=useState('')
    const [contact,setContact]=useState('')
    const [telContact,setTelContact]=useState('')

    const [allBanque,]=useState([
        {id: 1, value: 'BOA Madagascar'},
        {id: 2, value: 'BNI Madagascar'},
        {id: 3, value: 'Société générale Madagasikara (BFV - SG)'},
        {id: 4, value: 'BMOI'},
        {id: 5, value: 'AccèsBanque Madagascar'},
    ])

    useEffect(()=>{
        if (queryParams.size===0){
            navigate("/inscription/")
            return
        }else if (queryParams.size>13){
            setEntre(new Date(queryParams.get('entre')))
            setCnaps(queryParams.get('numCanps'))
            setBanque(JSON.parse(decodeURIComponent(queryParams.get('banque') || '{}')))
            setRib(queryParams.get('rib'))
            setContact(queryParams.get('contact'))
            setTelContact(queryParams.get('telContact'))
        }else{
            setBanque(allBanque[0])
        }
        setText("Information RH")
    },[])

    const [validated, setValidated] = useState(false);
    const formatDateToInputValue = (date) => {
        const year = String(date.getFullYear()).padStart(4, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };
    const handleInputChange = (e, setValue) => {
        let value = e.target.value;
        let parts = value.split('-');

        if (parts.length === 3) {
            parts[0] = parts[0] === '' ? '0000' : parts[0].padStart(4, '0');
            parts[1] = parts[1] === '' ? '01' : parts[1].padStart(2, '0');
            parts[2] = parts[2] === '' ? '01' : parts[2].padStart(2, '0');
        }
        const formattedValue = parts.join('-');
        setValue(new Date(formattedValue));
    };
    const handleSubmit = (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        }else{
            queryParams.set('entre', formatDateToInputValue(entre))
            queryParams.set('numCanps',numCanps)
            queryParams.set('banque', JSON.stringify(banque))
            queryParams.set('rib',rib)
            queryParams.set('contact',contact)
            queryParams.set('telContact',telContact)
            navigate(`/inscription/login-info?${queryParams.toString()}`)
        }

        setValidated(true);
    };
    return(
        <>
            <Form noValidate validated={validated} autoComplete={"off"} onSubmit={handleSubmit}>
                <Row className="mb-3">
                    <Form.Group className="mb-3" as={Col} md="6" controlId="entre">
                        <Form.Label className=" fw-bold">Date d'entrée</Form.Label>
                        <Form.Control
                            required
                            type="date"
                            className="bg-transparent text-white border-dark-light"
                            onChange={(e)=>handleInputChange(e,setEntre)}
                            value={formatDateToInputValue(entre)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="cnaps">
                        <Form.Label className=" fw-bold">Numeros CNAPS</Form.Label>
                        <Form.Control
                            type="text"
                            className="bg-transparent text-white border-dark-light"
                            placeholder="2321 1056 456"
                            value={numCanps}
                            onChange={(e)=>{
                                let value=e.target.value
                                value = value.replace(/\D/g, '')
                                setCnaps(value.replace(/(.{4})/g, '$1 ').trim())
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="banque">
                        <Form.Label className="fw-bold">Banque</Form.Label>
                        <Form.Select value={banque?.id} onChange={(html)=>setBanque(allBanque.find((element)=>element.id===parseInt(html.target.value,10)))} className="bg-transparent text-white border-dark-light" required>
                            {allBanque.map((e)=>(
                                <option key={e.id} className="text-dark" value={e.id}>{e.value}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="rib">
                        <Form.Label className="fw-bold">Numeros RIB</Form.Label>
                        <Form.Control
                            type="text"
                            className="bg-transparent text-white border-dark-light"
                            placeholder="5646 5112 12"
                            value={rib}
                            onChange={(e)=>{
                                let value=e.target.value
                                value = value.replace(/\D/g, '')
                                setRib(value.replace(/(.{4})/g, '$1 ').trim())
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="nom">
                        <Form.Label className="fw-bold">Nom (Contact en cas d'urgence)</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent text-white border-dark-light"
                            placeholder="Rakoto"
                            value={contact}
                            onChange={(e)=>setContact(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="tel">
                        <Form.Label className="fw-bold">Tél (Contact en cas d'urgence)</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent text-white border-dark-light"
                            placeholder="+261 3* ** *** **"
                            value={telContact}
                            onChange={(e)=>{
                                let value = e.target.value.slice(1).replace(/\D/g, '');
                                if (value.length > 12) value = value.slice(0, 12);
                                const formatted = value.slice(3).replace(/(\d{2})(\d{2})(\d{3})(\d{2})/, '$1 $2 $3 $4');
                                setTelContact('+261 ' + formatted);
                            }}
                        />
                    </Form.Group>
                </Row>
                <Row>
                    <Col md={6}>
                        <button type="button" onClick={()=>{navigate(`/inscription/?${queryParams.toString()}`)}} className="btn btn-lg bg-aris text-white w-100">Précedent</button>
                    </Col>
                    <Col md={6}>
                        <button type="submit" className="btn btn-lg bg-blue text-white w-100">Suivant</button>
                    </Col>
                </Row>
            </Form>
        </>
    )
}

export default InfoBanque