import {APIURL} from "./APIURL";

export default class Conge {
    dateDebut
    dateFin
    idEmp
    cause
    isMatinDebut
    isMatinFin

    constructor(dateDebut, dateFin, idEmp, cause, isMatinDebut, isMatinFin) {
        this.dateDebut = dateDebut;
        this.dateFin = dateFin;
        this.idEmp = idEmp;
        this.cause = cause;
        this.isMatinDebut = isMatinDebut;
        this.isMatinFin = isMatinFin;
    }

    static async addConge(allInfo) {
        const response = await fetch(APIURL() + "conge/save" , {
            method: 'post',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
            body: JSON.stringify(allInfo)
        });

       if (response.ok) {
            return await response.json();
        }else{
            throw new Error(await response.text());
        }
    }

    static async getConge(idEmp) {
        const response = await fetch(APIURL() + "conge/"+idEmp , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json();
        }else{
            throw new Error("on ne peut pas faire cette requete pour le moment");
        }
    }

    static async getSold(idEmp) {
        const response = await fetch(APIURL() + "conge/solde/"+idEmp , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.text();
        }else{
            throw new Error("on ne peut pas faire cette requete pour le moment");
        }
    }

    static async getPerMonth(idEmp, an) {
        const response = await fetch(APIURL() + "conge/perMonth/"+an+"/"+idEmp , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json();
        }else{
            throw new Error("on ne peut pas faire cette requete pour le moment");
        }
    }

    static async getInfoMonth(idEmp, an, mois) {
        const response = await fetch(APIURL() + "conge/info/"+idEmp+"/"+mois+"/"+an , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json();
        }else{
            throw new Error("on ne peut pas faire cette requete pour le moment");
        }
    }

    static async deleteConge(idConge) {
        const response = await fetch(APIURL() + "conge/delete/"+idConge, {
            method: 'delete',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.text();
        }else{
            throw new Error("on ne peut pas faire cette requete pour le moment");
        }
    }
}