import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import { Col, Dropdown, Form, Spinner, Toast} from "react-bootstrap";
import Pagination from "../Main/Utilitaire/Pagination";
import {UserContext} from "../Main/Utilitaire/UserProvider";
import { setHours,setMinutes, differenceInCalendarDays, addDays } from 'date-fns';
import Conge from "../../Model/Conge";
import {log} from "electron-log";

const formatDateToInputValue = (date) => {
    const year = String(date.getFullYear()).padStart(4, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
};
const formatNumberDay = (value, isDebut,isFin) => {
    if (!isDebut && !isFin){
        return Math.floor(value)+1
    }
    return Math.floor(value)
}
const Demande = () => {
    const { user: userContext }=useContext(UserContext)
    const [dateDebut,setDateDebut]=useState(new Date())
    const [dateFin,setDateFin]=useState(new Date())
    const [isMatinDebut,setMatinDebut]=useState(true)
    const [isMatinFin,setMatinFin]=useState(false)
    const [cause,setCause]=useState('')
    const [dateRetour,setDateRetour]=useState('')
    // const [isModi,setIsModif]=useState(true)
    const [erreur,setErreur]=useState(false)
    const [errorMessage,setErrorMessage]=useState('')
    const [loading,setLoading]=useState(false)

    const[congeData,setData]=useState([])
    const dure = useMemo( () => {
        let diffDays = differenceInCalendarDays(dateFin, dateDebut);
        if (isMatinDebut && isMatinFin){
            diffDays+=0.5;
        }else if(isMatinDebut && !isMatinFin){
            diffDays+=1;
        }else if(!isMatinDebut && !isMatinFin){
            diffDays+=0.5;
        }
        const valiny=Math.max(diffDays, 0)
        setDateRetour( ()=>{
            if (valiny===0){
                return "--/--/---- : --"
            }
            const format=formatDateToInputValue(addDays(dateDebut, formatNumberDay(valiny, isMatinDebut,isMatinFin))).split("-")
            return format[2]+"/"+format[1]+"/"+format[0]+`${isMatinFin ? ' : Midi' : ' : Matin'}`
        });
        return valiny;
    },[dateDebut, dateFin, isMatinDebut, isMatinFin])

    const [congeClicked, setCongeClicked]=useState(null)
    const submit = (event) => {
        event.preventDefault()
        if (dure===0){
            return
        }
        setLoading(true)
        const conge=new Conge(dateDebut, dateFin,userContext , cause, isMatinDebut,isMatinFin)
        Conge.addConge(conge).then((res)=>{
            setData((prev)=>[res,...prev])
            setDateRetour('')
            setCause('')
            setMatinDebut(true)
            setMatinFin(false)
            setDateDebut(new Date())
            setDateFin(new Date())
        }).catch((e)=>{
            setErrorMessage(e.message)
            setErreur(true)
        }).finally(()=>{
            setLoading(false)
        })
    }
    const closeError=useCallback(()=>{
        setErreur(false)
    },[])

    useEffect(()=>{
       Conge.getConge(userContext?.idEmp).then((resp)=>{
           setData(resp)
       }).catch((e)=>{
           console.log(e.message)
       })
    },[userContext])
    const [active,setActive]=useState(1)

    const deleteConge=useCallback((idConge)=>{
        if (idConge){
            Conge.deleteConge(idConge).then((res)=>{
                setData((prevData) => prevData.filter((item) => item.idConge !== idConge));
            }).catch((e)=>{
                setErreur(true)
                setErrorMessage(e.message)
            })
        }
    },[])

    return(
        <>
            <div className="card mb-3">
                <div className="card-body">
                    <h3 className="text-center pb-1 small fs-4">Demande de congé</h3>
                    <hr className="w-50 mx-auto pb-2"/>
                    <div className="row m-0 p-0">
                        <div className="col-4 border-end ">
                            <h4 className="text-center">Nouvelle demande</h4>
                            <Form onSubmit={(event)=>submit(event)}>
                                <Form.Group className="mb-1" controlId="formBasicEmail">
                                    <Form.Label>Date debut</Form.Label>
                                    <Form.Control type="date" value={formatDateToInputValue(dateDebut)} onChange={(event)=>{
                                        setDateDebut(setHours(setMinutes(new Date(event.target.value), 0), 0))
                                    }} placeholder="Date debut" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="DateDebutRadio">
                                    <Form.Check
                                        inline
                                        label="Matin"
                                        name="group1"
                                        type={"radio"}
                                        checked={isMatinDebut===true}
                                        onChange={()=>setMatinDebut(true)}
                                        id="radio-1"
                                    />
                                    <Form.Check
                                        inline
                                        label="Après-midi"
                                        name="group1"
                                        type={"radio"}
                                        checked={isMatinDebut===false}
                                        onChange={()=>setMatinDebut(false)}
                                        id="radio-2"
                                    />
                                </Form.Group>

                                <Form.Group className="mb-1" controlId="formBasicPassword">
                                    <Form.Label>Date fin</Form.Label>
                                    <Form.Control type="date" value={formatDateToInputValue(dateFin)} onChange={(e)=>{
                                        setDateFin(setHours(setMinutes(new Date(e.target.value), 0), 12))
                                    }} placeholder="Date fin" />
                                </Form.Group>
                                <Form.Group className="mb-2" controlId="DateFinRadio">
                                    <Form.Check
                                        inline
                                        name={"groupe2"}
                                        label="Matin"
                                        type={"radio"}
                                        checked={isMatinFin===true}
                                        onChange={()=>setMatinFin(true)}
                                        id="radio-12"
                                    />
                                    <Form.Check
                                        inline
                                        name={"groupe2"}
                                        label="Après-midi"
                                        type={"radio"}
                                        checked={isMatinFin===false}
                                        onChange={()=>setMatinFin(false)}
                                        id="radio-22"
                                    />
                                </Form.Group>
                                <div className="row m-0 p-0 w-100">
                                    <Form.Group as={Col} sm={4} className="ps-0 mb-3" controlId="NombreJour">
                                        <Form.Label>Durée</Form.Label>
                                        <Form.Control type="number" disabled value={dure}  placeholder="Nombre de jours" />
                                    </Form.Group>
                                    <Form.Group as={Col} sm={8} className="pe-0 mb-3" controlId="dateRetour">
                                        <Form.Label>Date retour</Form.Label>
                                        <Form.Control type="text" disabled value={dateRetour}  placeholder="Date retour au travail" />
                                    </Form.Group>
                                </div>

                                <Form.Group className="mb-3" controlId="Cause">
                                    <Form.Label>Cause (Optionnel)</Form.Label>
                                    <Form.Control value={cause} onChange={(e)=>setCause(e.target.value)} as={"textarea"} rows={3} placeholder="Cause" />
                                </Form.Group>
                                <div className="row m-0 p-0 w-100 d-flex justify-content-end">
                                    <button type={loading ? 'button' : 'submit'} className="btn bg-blue text-white w-100">
                                        {loading ? (
                                            <Spinner size={"sm"} animation="border" role="status">
                                                <span className="visually-hidden">Loading...</span>
                                            </Spinner>
                                        ) : ('Envoyer')}
                                    </button>
                                </div>
                            </Form>
                        </div>
                        <div className="col-8">
                            <h4 className="text-center mb-2">liste de précedent demandes</h4>
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">#</th>
                                        <th scope="col">De </th>
                                        <th scope="col">Au </th>
                                        <th scope="col">Satut</th>
                                        <th scope="col"/>
                                    </tr>
                                </thead>
                                <tbody>
                                    {congeData.slice((active-1)*10,(active*10)).map((element,index)=>(
                                        <tr key={index}>
                                            <th scope="row">{element.dateDemande}</th>
                                            <td><pre className="p-0 my-auto mx-0 fs-6">{element.dateDebut} {element.matinDebut ? 'Matin' : 'Midi'}</pre></td>
                                            <td><pre className="p-0 my-auto mx-0 fs-6">{element.dateFin} {element.matinFin ? 'Matin' : 'Midi'}</pre></td>
                                            <td><span className={`px-2 py-1 my-auto rounded-2 text-white ${element.statut==="REFUSER" ? 'bg-danger' :element.statut==='ACCEPTER' ? 'bg-success': 'bg-warning' }`}>{element.statut}</span></td>
                                            <td>
                                                <Dropdown>
                                                    <Dropdown.Toggle
                                                        id={`dropdown-${index}`}
                                                        as={"span"}
                                                        className="px-2 py-1 my-auto border rounded-2 text-muted cursor-pointer"
                                                    >
                                                        Option
                                                    </Dropdown.Toggle>
                                                    <Dropdown.Menu>
                                                        {element.statut==="ENCOURS" && (
                                                            <>
                                                                <Dropdown.Item onClick={()=>setCongeClicked(element)} >Editer</Dropdown.Item>
                                                                <Dropdown.Item onClick={()=>deleteConge(element.idConge)}>Supprimer</Dropdown.Item>
                                                            </>
                                                        )}
                                                        <Dropdown.Item onClick={()=>setCongeClicked(element)} >Voir</Dropdown.Item>
                                                    </Dropdown.Menu>
                                                </Dropdown>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                            <div className="d-flex justify-content-end w-100 m-0 p-0">
                                <Pagination length={Math.floor((congeData.length/10))+1} active={active} setActive={setActive}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="position-fixed bottom-0 end-0 width-max p-2">
                <Toast bg={"danger"} show={erreur} onClose={closeError} autohide delay={5000}>
                    <Toast.Header>
                        <i className="rounded me-2 text-aris icon-30 fas fa-circle-exclamation"/>
                        <strong className="me-auto">Erreur</strong>
                        <small className="text-muted">Maintenant</small>
                    </Toast.Header>
                    <Toast.Body className><strong>{errorMessage}</strong></Toast.Body>
                </Toast>
            </div>
        </>
    )
}
export default Demande