import React, {useEffect, useState} from "react";
import { Col, Form, Row, Spinner, Toast} from "react-bootstrap";
import logo from '../img/logoA.png'
import {useNavigate} from "react-router-dom";
import LoginMD from "../Model/LoginMD";
import arisConcept from '../img/ArisConcept.png'
const Login = () => {
    const navigate=useNavigate()
    const [login,setLogin]=useState('')
    const [mdp,setMdp]=useState('')
    const [loading,setLoading]=useState(false)
    const [erreur,setErreur]=useState(false)
    const [textErreur,setTextErreur]=useState('')

    const [isValid,setValid]=useState(false)
    const [loadToken,setLoadToken]=useState(true)
    useEffect(()=>{
        setLoadToken(true)
        LoginMD.isTokenValid().then((res)=>{
            navigate("/accueil")
        }).catch(()=>{
            window.electronStore.clear()
        }).finally(()=>{
            window.main.maximise()
            setLoadToken(false)
        })
    },[])
    const handleSubmit = (event) => {
        const form = event.currentTarget;
        event.preventDefault();
        if (form.checkValidity() === false) {
            event.stopPropagation();
        }else{
            setLoading(true)
            LoginMD.login(login,mdp).then((resp)=>{
                if (resp){
                    window.electronStore.set('token', resp);
                    // localStorage.setItem('token', resp)
                    navigate("/accueil")
                }
            }).catch((e)=>{
                setErreur(true)
                setTextErreur(e.message)
            }).finally(()=>{
                setLoading(false)
            })
        }
    };
    useEffect(()=>{
        setValid(erreur)
    },[erreur])
    return(
        <>
            <div className="d-flex justify-content-center align-items-center min-vh-100 w-100">
                <div className="row m-0 p-0 w-100">
                    {loadToken ? (
                        <div className="w-100 content">
                            <img src={arisConcept} className="rounded mx-auto d-block img-logo" alt="logo"/>
                        </div>
                    ): (
                        <div className="col-6 content offset-3">
                            <div className="card bg-image shadow-lg rounded-3">
                                <Row className="p-1 mt-1 d-inline-flex justify-content-center mx-0">
                                    <h4 className="text-center"><img className="card-img me-2 p-1" src={logo} alt=""/><span className="text-aris">Aris</span> <span className="text-concept">Concept</span></h4>
                                    <span className="text-center w-100 dark-light small "><span className="divider"/> Se connecter <span className="divider"/></span>
                                </Row>
                                <div className="card-body rounded-3 p-3 m-3 mt-0">
                                    <Form autoComplete={"off"} onSubmit={handleSubmit}>
                                        <Row className="mb-3">
                                            <Form.Group className="mb-3" as={Col} sm="12" controlId="validationCustom01">
                                                <Form.Label aria-label={'Login / Email'} className="dark-light fw-bold">Login / Email</Form.Label>
                                                <Form.Control
                                                    required
                                                    type="text"
                                                    className="bg-transparent border-dark-light"
                                                    placeholder="email"
                                                    size={"lg"}
                                                    value={login}
                                                    onChange={(e)=>setLogin(e.target.value)}
                                                    isInvalid={isValid}
                                                />
                                            </Form.Group>
                                            <Form.Group className="mb-3" as={Col} sm="12" controlId="validationCustom02">
                                                <Form.Label aria-label={'Mot de passe'} className="dark-light fw-bold">Mot de passe</Form.Label>
                                                <Form.Control
                                                    required
                                                    type="password"
                                                    size={"lg"}
                                                    className="bg-transparent border-dark-light"
                                                    placeholder="mot de passe"
                                                    value={mdp}
                                                    onChange={(e)=>setMdp(e.target.value)}
                                                    isInvalid={isValid}
                                                />
                                            </Form.Group>
                                        </Row>
                                        <Row>
                                            <button type={loading ? 'text' : "submit"} className="btn btn-lg bg-blue text-white">
                                                {loading ? (
                                                    <Spinner size={'sm'} animation="border" role="status">
                                                        <span className="visually-hidden">Loading...</span>
                                                    </Spinner>
                                                ): ('Connexion')}
                                            </button>
                                        </Row>
                                        <div className="row mt-2 mb-0 mx-0 text-center p-0 w-100">
                                            <button className="btn link-primary-in border-0" onClick={()=>navigate('/inscription')}>Inscription</button>
                                        </div>
                                    </Form>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>
            <div className="position-fixed bottom-0 end-0 width-max p-2">
                <Toast bg={"danger"} show={erreur} onClose={()=>setErreur(false)} autohide delay={5000}>
                    <Toast.Header>
                        <i className="rounded me-2 text-aris icon-30 fas fa-circle-exclamation"/>
                        <strong className="me-auto">Erreur</strong>
                        <small className="text-muted">Maintenant</small>
                    </Toast.Header>
                    <Toast.Body className><strong>{textErreur}</strong></Toast.Body>
                </Toast>
            </div>
        </>
    )
}
export default Login