import React, {useEffect, useState} from "react";
import {Collapse} from "react-bootstrap";
import {useComponentState} from "../../Route/StateContext";
import {generateUniqueId} from "../../Route/Util";

const LiElement = ({isActive=false, icon, text, children}) => {
    // const [id, setId] = useState(() => generateUniqueId());
    const [open, setOpen] = useState( false)

    // useEffect(() => {
    //     const storedId = localStorage.getItem(`liElementId-${text}`);
    //     if (storedId) {
    //         setId(storedId);
    //     } else {
    //         localStorage.setItem(`liElementId-${text}`, id);
    //     }
    // }, [id, text]);
    return (
        <>
            <li onClick={() => setOpen(!open)} className={`list-group-item li ${isActive ? 'list-group-item-active' : ''} border-0 my-1 rounded-2 cursor-pointer d-flex align-items-center`}>
                <div className="icon-start">
                    <i className={`fas ${icon} icon-30 px-2`}/>
                </div>
                <div className="text flex-grow-1 ms-2 pb-1">
                    <span className="fs-5 fw-bold">{text}</span>
                </div>
                <div className="caret">
                    <i className={`fas ${open ? 'fa-angle-up' : 'fa-angle-down'} fw-bold`} />
                </div>
            </li>
            <Collapse in={open} mountOnEnter unmountOnExit>
                <div id="example-collapse-text">
                    {children}
                </div>
            </Collapse>
        </>
    )
}

export default LiElement