import Login from "./Page/Login";
// import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Bg from "./Component/Bg";
import Home from "./Page/Home";
import Inscription from "./Page/Inscription";
import InfoPersonel from "./Component/InfoPersonel";
import InfoBanque from "./Component/InfoBanque";
import InfoLogin from "./Component/InfoLogin";
import {useState} from "react";
import Content from "./Component/Content";
import ValiderSalarie from "./Component/Main/ValiderSalarie";
import AttendanceCalendar from "./Component/Main/Presence/AttendanceCalendar";
import ListSalarier from "./Component/Main/Salarier/ListSalarier";
import Solde from "./Component/Conge/Solde";
import Demande from "./Component/Conge/Demande";

function App() {
    const [textInscription,setText]=useState('inscription')

    return (
        <>
            <Router>
                <Routes>
                    <Route path="/" element={<Bg />}>
                        <Route path="" element={<Login />} />
                        <Route path="/inscription" element={<Inscription text={textInscription} />}>
                            <Route path="" element={<InfoPersonel setText={setText}/>} />
                            <Route path="rh-info" element={<InfoBanque setText={setText}/>} />
                            <Route path="login-info" element={<InfoLogin setText={setText} />} />
                        </Route>
                        <Route path="/*" element={<Home />} >
                            <Route path="accueil" element={<Content />} />
                            <Route path="employer/list" element={<ListSalarier />} />
                            <Route path="employer/precence" element={<AttendanceCalendar />} />
                            <Route path="conge/solde" element={<Solde />} />
                            <Route path="conge/demande" element={<Demande />} />
                        </Route>
                    </Route>
                </Routes>
            </Router>
        </>
    );
}

export default App;
