import {APIURL} from "./APIURL";

export default class EtatEmployer {
    static async getEmployee() {
        const response = await fetch(APIURL() + "employe/all" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json'
            }
        });

        if (response.ok) {
            return await response.json();
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
}