import React, {useEffect, useMemo, useState} from "react";
import { Modal } from "react-bootstrap";
import ModifInfoPersonel from "./ModifInfoPersonel";
import ModifRh from "./ModifRh";
import ModifLog from "./ModifLog";

const Modif = ({ show, close, object, changeValueUser, isModifier }) => {
    const [actif, setActif] = useState(1);
    useEffect(()=>{
        if (show){
            setActif(1)
        }
    },[show])

    const modalTitle = useMemo(() => {
        switch (actif) {
            case 1:
                return "Modification De Vos Infos";
            case 2:
                return "Modification";
            case 3:
                return "Modification De votre login";
            default:
                return "Modification";
        }
    }, [actif]);

    const modalContent = useMemo(() => {
        if (object){
            switch (actif) {
                case 1:
                    return <ModifInfoPersonel setActif={setActif} close={close} object={object} changeValueUser={changeValueUser} isModifier={isModifier} />;
                case 2:
                    return <ModifRh setActif={setActif} object={object} close={close}  changeValueUser={changeValueUser} isModifier={isModifier} />;
                case 3:
                    return <ModifLog setActif={setActif} object={object} close={close} changeValueUser={changeValueUser} isModifier={isModifier}/>;
                default:
                    return null;
            }
        }
        return null
    }, [actif,object]);
    return (
        <Modal
            show={show}
            onHide={close}
            keyboard={false}
            backdrop="static"
            dialogClassName="modal-dialog-centered modal-90w position-relative d-flex justify-content-center align-items-center"
            contentClassName="d-flex justify-content-center w-100"
        >
            <Modal.Header closeButton>
                <Modal.Title className="text-aris fs-5">{modalTitle}</Modal.Title>
            </Modal.Header>
            <Modal.Body>{modalContent}</Modal.Body>
        </Modal>
    );
};

export default Modif;
