import {APIURL} from "./APIURL";

export default class Pointage {
    dateDebut
    dateFin

    constructor(dateDebut, dateFin) {
        this.dateDebut = dateDebut;
        this.dateFin = dateFin;
    }

    static async getPointageNow(){
        const response = await fetch(APIURL() + "pointage/me/now" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json()
        }else{
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }

    static async getPointagePerMonth(idEmp,month,year){
        const response = await fetch(APIURL() + `pointage/month/${idEmp}/${month}/${year}` , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json()
        }else{
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
}