import React from "react";

const Pagination = ({length,active, setActive}) => {
    length=Math.floor(length)
    return(
        <>
            {length>0 ? (
                <ul className="pagination mb-0">
                    <li className="page-item d-flex justify-content-center my-auto">
                        <i onClick={()=>setActive((prev)=>{
                            if (prev<=1){
                                return 1
                            }
                            return prev-1
                        })} className="fas m-2px fa-chevron-left border-0 link-primary-in cursor-pointer me-2"/>
                    </li>
                    {Array.from({ length: length }, (_, index) => (
                        <li key={index} className="page-item"><span onClick={()=>setActive(index+1)} className={`page-link cursor-pointer rounded-circle width-height-sam d-flex justify-content-center border-0 ${(index+1)===active ? 'pagination-active' : ''}`}>{index+1}</span></li>
                    ))}
                    <li className="page-item d-flex justify-content-center my-auto">
                        <i onClick={()=>setActive((prev)=>{
                            if (prev>=(length-1)){
                                return length
                            }
                            return prev+1
                        })} className="fas m-2px fa-chevron-right border-0 link-primary-in cursor-pointer ms-2"/>
                    </li>
                </ul>
            ): ('')}
        </>
    )
}
export default Pagination