import React, {useEffect, useState} from "react";
import {Col, Form, Row} from "react-bootstrap";
import Type from "../../../Model/Type";
import Allinfo from "../../../Model/Allinfo";

const ModifInfoPersonel = ({setActif,object, changeValueUser, isModifier,close}) => {
    const[post,setPost]=useState([])
    const [genres,]=useState([
        {id: 1, genre: 'Homme'},
        {id: 2, genre: 'Femme'},
        {id: 3, genre: 'Autre'},
    ])
    const [statuts,]=useState([
        {id: 1, value: 'Célibataire'},
        {id: 2, value: 'Marié(e)'},
        {id: 3, value: 'Séparé(e)'},
        {id: 4, value: 'Veuf/veuve'},
        {id: 5, value: 'En union libre'},
    ])
    const [niveaus,]=useState([
        {id: 1, value: '< Bac'},
        {id: 2, value: 'Bac +'},
        {id: 3, value: 'Licence'},
        {id: 4, value: 'Master I'},
        {id: 5, value: 'Master II'},
    ])
    const [clickTerminer,setClickTerminer]=useState(0)
    const [postSelected,setPostSelected]=useState(null)
    const [nom,setNom]=useState('')
    const [prenom,setPrenom]=useState('')
    const [naissance,setNaissance]=useState(new Date())
    const [tel,setTel]=useState('')
    const [adresse,setAdresse]=useState('')
    const [cin,setCin]=useState('')
    const [valide,setValide]=useState(new Date())
    const [nationalite,setNationalite]=useState('')
    const [genre,setGenre]=useState(null)
    const [statut,setStatut]=useState(null)
    const [enfant,setEnfant]=useState(0)
    const [niveau,setNiveau]=useState(null)

    const formatDateToInputValue = (date) => {
        const year = String(date.getFullYear()).padStart(4, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };
    const handleInputChange = (e, setValue) => {
        let value = e.target.value;
        let parts = value.split('-');

        if (parts.length === 3) {
            parts[0] = parts[0] === '' ? '0000' : parts[0].padStart(4, '0');
            parts[1] = parts[1] === '' ? '01' : parts[1].padStart(2, '0');
            parts[2] = parts[2] === '' ? '01' : parts[2].padStart(2, '0');
        }
        const formattedValue = parts.join('-');
        setValue(new Date(formattedValue));
    };

    useEffect(()=>{
        Type.getAllPostes().then((res)=>{
            setPost(res)
        }).catch((e)=>{
            console.log(e)
        })
        if (object){
            setPostSelected(object.type)
            setNom(object.nom)
            setPrenom(object.prenom)
            setNaissance(new Date(object.naissance))
            setTel(object.tel)
            setAdresse(object.adresse)
            setCin(object.cin)
            setValide(new Date(object.valide))
            setNationalite(object.nationalite)
            setGenre(object.genre)
            setStatut(object.statut)
            setEnfant(parseInt(object.enfant || '0', 10))
            setNiveau(object.niveau)
        }
    },[])

    const [validated, setValidated] = useState(false);

    const changeValueObject = () => {
        changeValueUser({
            ...object,
            nom: nom,
            prenom: prenom,
            type: postSelected,
            naissance: naissance,
            tel: tel,
            adresse: adresse,
            cin: cin,
            valide: valide,
            nationalite: nationalite,
            genre: genre,
            statut: statut,
            enfant: enfant,
            niveau: niveau
        })
    }
    const handleSubmit = (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        } else {
            if (clickTerminer===1){
                Allinfo.modifEmployee({...object,
                    nom: nom,
                    prenom: prenom,
                    type: postSelected,
                    naissance: naissance,
                    tel: tel,
                    adresse: adresse,
                    cin: cin,
                    valide: valide,
                    nationalite: nationalite,
                    genre: genre,
                    statut: statut,
                    enfant: enfant,
                    niveau: niveau
                },object.mdp).catch((error)=>{
                    console.log(error.message)
                }).finally(()=>{
                    close()
                    isModifier((prev)=>!prev)
                })
            }else if (clickTerminer===2){
                setActif(2)
                changeValueObject()
            }else{
                return
            }
        }
        setValidated(true);
    }
    return(
        <>
            <Form className="m-0 p-0" noValidate validated={validated} autoComplete={"off"} onSubmit={handleSubmit}>
                <Row className="mb-3 border-bottom">
                    <Form.Group className="mb-3" as={Col} md="4" controlId="poste">
                        <Form.Label className="fw-bold text-light-emphasis">Poste</Form.Label>
                        <Form.Select value={postSelected?.idType || ''} onChange={(html)=> setPostSelected(post.find((element)=>element.idType===parseInt(html.target.value, 10)))} className="bg-transparent border-dark-light" required>
                            {post.map((e)=>(
                                <option key={e.idType} className="text-dark" value={e.idType}>{e.poste}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="nom">
                        <Form.Label className="fw-bold text-light-emphasis">Nom</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="Rakoto"
                            value={nom}
                            onChange={(e)=>setNom(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="prenom">
                        <Form.Label className="fw-bold text-light-emphasis">Prénom</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="Randria"
                            value={prenom}
                            onChange={(e)=>setPrenom(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="naissance">
                        <Form.Label className="fw-bold text-light-emphasis">Date de naissance</Form.Label>
                        <Form.Control
                            required
                            type="date"
                            className="bg-transparent border-dark-light"
                            placeholder="mot de passe"
                            value={formatDateToInputValue(naissance)}
                            onChange={(e)=>handleInputChange(e,setNaissance)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="tel">
                        <Form.Label className="fw-bold text-light-emphasis">Tel</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="03xxxxxx-03xxxxxxx"
                            value={tel}
                            onChange={(e)=>{
                                let string=e.target.value
                                string= string.replace(/-/g, ' - ')
                                string= string.replace(/\s+/g, ' ').trim()
                                setTel(string)
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="adresse">
                        <Form.Label className="fw-bold text-light-emphasis">Adresse</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="Lot xxxxx xxxxx"
                            value={adresse}
                            onChange={(e)=>setAdresse(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="cin">
                        <Form.Label className="fw-bold text-light-emphasis">CIN</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="0000 0000 0000"
                            value={cin}
                            onChange={(e)=>{
                                let value=e.target.value
                                value = value.replace(/\D/g, '')
                                setCin(value.replace(/(.{4})/g, '$1 ').trim())
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="validation">
                        <Form.Label className="fw-bold text-light-emphasis">Date valide CIN</Form.Label>
                        <Form.Control
                            required
                            type="date"
                            className="bg-transparent border-dark-light"
                            value={formatDateToInputValue(valide)}
                            onChange={(e)=>handleInputChange(e,setValide)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="4" controlId="nationalite">
                        <Form.Label className="fw-bold text-light-emphasis">Nationalité</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="Malagasy"
                            value={nationalite}
                            onChange={(e)=>setNationalite(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="3" controlId="genre">
                        <Form.Label className="fw-bold text-light-emphasis">Genre</Form.Label>
                        <Form.Select value={genre?.id} onChange={(e)=>setGenre(genres.find((element)=>element.id===parseInt(e.target.value,10)))} className="bg-transparent border-dark-light" required>
                            {genres.map((e)=>(
                                <option key={e.id} className="text-dark" value={e.id}>{e.genre}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="3" controlId="statut">
                        <Form.Label className="fw-bold text-light-emphasis">Statut Marital</Form.Label>
                        <Form.Select value={statut?.id} onChange={(e)=>setStatut(statuts.find((element)=>element.id===parseInt(e.target.value,10)))} className="bg-transparent border-dark-light" required>
                            {statuts.map((e)=>(
                                <option key={e.id} className="text-dark" value={e.id}>{e.value}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="3" controlId="enfant">
                        <Form.Label className="fw-bold text-light-emphasis">Nombre enfant</Form.Label>
                        <Form.Control
                            required
                            type="number"
                            className="bg-transparent border-dark-light"
                            placeholder="0"
                            value={enfant}
                            onChange={(e)=>setEnfant(parseInt(e.target.value || "0",10))}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="3" controlId="etude">
                        <Form.Label className="fw-bold text-light-emphasis">Niveau d'étude</Form.Label>
                        <Form.Select value={niveau?.id} onChange={(e)=>setNiveau(niveaus.find((element)=>element.id===parseInt(e.target.value,10)))} className="bg-transparent border-dark-light" required>
                            {niveaus.map((e)=>(
                                <option key={e.id} className="text-dark" value={e.id}>{e.value}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                </Row>
                <Row className="w-100 m-0 p-0 mb-2">
                    <Col md={6}>
                        <button type="submit" onClick={()=>setClickTerminer(1)} className="btn bg-blue text-white w-100">Terminer</button>
                    </Col>
                    <Col md={6}>
                        <button type="submit" onClick={()=> {
                            setClickTerminer(2)
                        }} className="btn btn-info text-white w-100">Suivant</button>
                    </Col>
                </Row>
            </Form>
        </>
    )
}
export default ModifInfoPersonel