import {APIURL} from "./APIURL";

export default class LoginMD {
    login
    mdp

    constructor(login, mdp) {
        this.login = login;
        this.mdp = mdp;
    }

    static async login(userName,mdp) {
        const response = await fetch(APIURL() + "login/" , {
            method: 'post',
            headers: {
                "Content-Type": 'application/x-www-form-urlencoded'
            },
            body: new URLSearchParams({
                'login': userName,
                'mdp': mdp,
            }),
        })
        if (response.status===401) {
            throw new Error('Il y a une session ouverte avec ce login')
        }else if(response.status===500){
            throw new Error('Mot de passe ou login non autorisé')
        }else if(response.status===200){
            return await response.text()
        }else{
            throw new Error('Erreur interne du serveur')
        }
    }
    static async logout(){
        const response = await fetch(APIURL() + "login/logout" , {
            method: 'post',
            headers: {
               "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        })
        if (response.ok){
            return await response.text()
        }else{
            throw new Error("Erreur de déconnection!!")
        }
    }
    static async isTokenValid(){
        if (await window.electronStore.get('token')){
            const response = await fetch(APIURL() + "login/isValid" , {
                method: 'get',
                headers: {
                    "Authorization": 'Bearer '+ await window.electronStore.get('token')
                },
            })
            if (response.ok){
                const rep=(await response.text()).toLowerCase()==='true'
                if (!rep){
                    throw new Error("Debutez avec nouvelle session!")
                }
                return rep
            }else{
                throw new Error("Erreur de connexion!!")
            }
        }else{
            throw new Error("no log:")
        }
    }
}