import React from "react";
import { Row} from "react-bootstrap";
import logo from "../img/logoA.png";
import {Outlet} from "react-router-dom";

const Inscription = ({text}) => {

    return(
        <>
            <div className="d-flex justify-content-center align-items-center min-vh-100 w-100">
                <div className="row m-0 p-0 w-100">
                    <div className="col-10 offset-1">
                        <div className="card bg-image bg-transparent text-white shadow-lg rounded-3">
                            <Row className="p-1 mt-1 d-inline-flex justify-content-center mx-0">
                                <h4 className="text-center"><img className="card-img me-2 p-1" src={logo} alt=""/><span className="text-aris">Aris</span> <span className="text-concept">Concept</span></h4>
                                <span className="text-center w-100 text-uppercase small "><span className="divider w-25"/> {text} <span className="divider w-25"/></span>
                            </Row>
                            <div className="card-body rounded-3 p-3 m-3 mt-0">
                                <Outlet />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Inscription