import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import {UserContext} from "../Main/Utilitaire/UserProvider";
import {useLocation} from "react-router-dom";
import Conge from "../../Model/Conge";
import { getDate, parseISO } from 'date-fns';
import {OverlayTrigger, Tooltip} from "react-bootstrap";

const Solde = () => {
    const { user: userContext }=useContext(UserContext)
    const [currentDate] = useState(new Date());
    const [year, setYear] = useState(currentDate.getFullYear());
    const [solde,setSolde]=useState(0)
    const { search } = useLocation();
    const [idUser,setIdUser]=useState(null)
    const [dateClicked, setDateClicked]=useState(null)

    const monthYear = useMemo(()=>{
        return ["Jan", "Fev", "Mar", "Avr", "Mai", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
    },[])
    const daysInWeek = useMemo(()=>{
        return ['Dim', 'Lun', 'Mar', 'Mer', 'Jeu', 'Ven', 'Sam']
    },[])

    const [monthClicked,setMonthClicked]=useState(0)
    const [values,setValues]=useState([])

    const handleClickValue = useCallback((value) => {
        setMonthClicked(value)
    },[])

    useEffect(()=>{
        if (idUser && monthClicked){
            Conge.getInfoMonth(idUser?.id, year, monthClicked).then((res)=>{
                setDateClicked(res)
            }).catch((er)=>{
                console.log(er.message)
            })
        }
    },[idUser,monthClicked])

    const daysInMonth = useMemo(() => new Date(year, monthClicked, 0).getDate(), [year, monthClicked]);
    const firstDayOfMonth = useMemo(() => new Date(year, monthClicked - 1, 1).getDay(), [year, monthClicked]);
    const dates = useMemo(() => {
        const initialDates = Array(firstDayOfMonth).fill(null).concat(
            Array.from({ length: daysInMonth }, (_, i) => i + 1)
        );
        while (initialDates.length % 7 !== 0) {
            initialDates.push(null);
        }
        return initialDates;
    }, [daysInMonth, firstDayOfMonth]);
    useEffect(()=>{
        const queryParams = new URLSearchParams(search);
        if (queryParams.size===0){
            Conge.getSold(userContext?.idEmp).then((res)=>{
                setSolde(parseFloat(res))
            })
            setIdUser({id: userContext?.idEmp, name: userContext?.nom+" "+ userContext?.prenom})
        }else{
            const userId = queryParams.get("id");
            const name = queryParams.get("name");
            const firstName = queryParams.get("firstName");
            if (userId ) {
                setIdUser({id: userId, name: name+" "+ firstName});
                Conge.getSold(userContext?.idEmp).then((res)=>{
                    setSolde(parseFloat(res))
                })
            }
        }
    },[search])

    useEffect(()=>{
        setDateClicked(null)
        if (idUser){
            Conge.getPerMonth(idUser?.id, year).then((resp)=>{
                if (resp){
                    setValues(resp)
                }
            }).catch((e)=>{
                console.log(e.message)
            })
        }
        setMonthClicked(0)
    },[year, idUser])

    function isComprise(jour, plagesDeDates) {
        const plageTrouvee = plagesDeDates.find(({ dateDebut, dateFin }) => {
            const debut = getDate(parseISO(dateDebut));
            const fin = getDate(parseISO(dateFin));
            return jour >= debut && jour <= fin;
        });

        if (plageTrouvee) {
            return {
                comprise: true,
                cause: plageTrouvee.cause,
                dateDemande: plageTrouvee.dateDemande
            };
        }

        return {
            comprise: false,
            cause: null,
            dateDemande: null
        };
    }
    return (
        <>
            <div className="card mb-3">
                <div className="card-body">
                    <h3 className="text-center pb-1 small fs-5">Solde de Congé : <span className="m-0 p-0 small fs-5 text-capitalize">{idUser?.name}</span></h3>
                    <hr className="w-50 mx-auto pb-2"/>
                    <div className="row m-0 p-0 mb-1">
                        <div className="col-7 ps-0">
                            <div className="p-2 border rounded-3 w-100">
                                <div className="centered-year-container my-2">
                                    <div>
                                        <i onClick={() => setYear(prev => prev - 1)} className="fas fa-chevron-left fs-5 cursor-pointer"/>
                                    </div>
                                    <div className="year">
                                        <pre className="text-dark fs-5 m-0 p-0">{year}</pre>
                                    </div>
                                    <div>
                                        <i onClick={() => setYear(prev => prev + 1)} className="fa fa-chevron-right fs-5 cursor-pointer"/>
                                    </div>
                                </div>
                                <div className="day-content pb-2 border-bottom">
                                    <table className="days-table w-100">
                                        {Array.from({length: 2},(_,v)=>(
                                            <React.Fragment key={v}>
                                                <thead>
                                                    <tr>
                                                        {monthYear.slice(v*6,(v+1)*6).map((day, index) => (
                                                            <th key={index} scope="col" className={`day p-0 ${((v*6)+index+1)===monthClicked ? 'text-aris fw-bolder': ''}`}>{day}</th>
                                                        ))}
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        {values.slice(v*6,(v+1)*6).map((value,index)=>(
                                                            <td key={index} className={`date-cell fw-bold`}>
                                                                {value!==0 ? (
                                                                    <div onClick={()=>handleClickValue((v*6)+index+1)} className={`date-content shadow cursor-pointer`}>
                                                                        <pre className="m-0 p-0">{value || ''}</pre>
                                                                    </div>
                                                                ) : ('--')}
                                                            </td>
                                                        ))}
                                                    </tr>
                                                </tbody>
                                            </React.Fragment>
                                        ))}
                                    </table>
                                </div>
                                <div className="row my-1 mx-0 p-0 w-100">
                                    <ul className="list-group list-group-horizontal justify-content-between">
                                        <li className="list-group-item border-0 bg-transparent d-inline-flex ms-0 ps-0">
                                            <div className="date-content shadow d-flex justify-content-center">
                                                <pre className="m-0 p-0 fw-bolder">x</pre>
                                            </div>
                                            <div className="ps-2 my-auto">
                                                <pre className="m-0 p-0 fw-bolder">Nombre de congés pris dans un mois</pre>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="col-5 border rounded-3">
                            <ul className={`list-group list-group-flush ${monthClicked===0 ? 'h-100 d-flex justify-content-center' : 'top-9 bg-white width-max mx-auto'}`}>
                                <li className="list-group-item border-0 bg-transparent text-center px-2 py-1 w-100">
                                    <pre className="m-0 p-0 fs-5 fw-bolder"><span className="text-decoration-underline">Congé restant:</span> <span className="px-2 py-1 bg-info-subtle text-info rounded-3">{solde}</span>j</pre>
                                </li>
                            </ul>
                            {monthClicked!==0 && dateClicked!==null && (
                                <table className="days-table w-100 mt--10">
                                    <thead>
                                        <tr>
                                            {daysInWeek.map((day, index) => (
                                                <th key={index} className="day p-0">{day}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                    {Array.from({ length: dates.length / 7 }, (_, weekIndex) => (
                                        <tr key={weekIndex}>
                                            {dates.slice(weekIndex * 7, (weekIndex + 1) * 7).map((id, index) => {
                                                const comp= isComprise(id,dateClicked)
                                                return (
                                                    <td key={index}
                                                        className={`date-cell ${id ? 'cursor-pointer' : 'empty-cell'}`}
                                                    >
                                                        {id ? comp.comprise ? (
                                                            <OverlayTrigger
                                                                placement="bottom"
                                                                overlay={
                                                                    <Tooltip id={`button-tooltip-${id}`} >
                                                                        <div>
                                                                            <span>{comp.cause}</span>
                                                                            <p className="p-0 m-0">{comp.dateDemande}</p>
                                                                        </div>
                                                                    </Tooltip>
                                                                }
                                                            >
                                                                <div  className={`date-content shadow bg-info`}>
                                                                    <pre className="m-0 p-0">{id || ''}</pre>
                                                                </div>
                                                            </OverlayTrigger>
                                                        ) : (
                                                            <div  className={`date-content shadow`}>
                                                                <pre className="m-0 p-0">{id || ''}</pre>
                                                            </div>
                                                        ) : ('')}
                                                    </td>
                                                );
                                            })}
                                        </tr>
                                    ))}
                                    </tbody>
                                </table>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Solde