import React, {useContext, useEffect, useState} from "react";
import PointageCalendar from "../Component/PointageCalendar";
import {UserContext} from "../Utilitaire/UserProvider";
import {useLocation} from "react-router-dom";

const AttendanceCalendar = React.memo( () => {
    const { user: contextUser } = useContext(UserContext);
    const [user, setUser] = useState(null);
    const { search } = useLocation();

    useEffect(()=>{
        const queryParams = new URLSearchParams(search);
        if (queryParams.size===0){
            setUser(contextUser)
        }else{
            const userId = queryParams.get("id");
            const name = queryParams.get("name");
            const firstName = queryParams.get("firstName");
            if (userId && name) {
                setUser({ idEmp: userId, nom: name, prenom: firstName });
            }
        }
    },[search])
    return (
        <>
            {user ? (
                <PointageCalendar linkUser={user?.idEmp} name={user?.nom + ' '+user?.prenom}/>
            ) : ('')}
        </>
    );
});

export default AttendanceCalendar;
