import React, {useCallback, useContext, useEffect, useState} from "react";
import {UserContext} from "../Utilitaire/UserProvider";
import {InputGroup, Form, Dropdown} from "react-bootstrap";
import Pagination from "../Utilitaire/Pagination";
import EtatEmployer from "../../../Model/EtatEmployer";
import {useNavigate} from "react-router-dom";

const ListSalarier = () => {
    const navigate=useNavigate()
    const { user, } = useContext(UserContext);
    const [active,setActive]=useState(1)
    const [allEmploye,setAll]=useState([])
    const [data,setData]=useState([])

    const [idS,setId]=useState('')
    const [nomS,setNom]=useState('')
    const [posteS,setPoste]=useState('')
    const [dateDebut,setDateDebut]=useState('')
    const [dateAu,setDateAu]=useState('')

    useEffect(()=>{
        EtatEmployer.getEmployee().then((res)=>{
            setAll(res)
        })
    },[])

    useEffect(() => {
        filterData();
    }, [idS, nomS, posteS, dateDebut, dateAu, allEmploye]);
    const normalizeString = useCallback(str=>{
        return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
    },[])
    const filterData = () => {
        let filteredData = allEmploye;
        if (idS) {
            filteredData = filteredData.filter(emp =>
                emp.emp.employe.idEmp===parseInt(idS,10)
            );
        }
        if (nomS) {
            filteredData = filteredData.filter(emp =>
                emp.emp.employe.nom.toLowerCase().includes(nomS.toLowerCase()) ||
                emp.emp.employe.prenom.toLowerCase().includes(nomS.toLowerCase())
            );
        }
        if (posteS) {
            filteredData = filteredData.filter(emp =>
                normalizeString(emp.emp.employe.type.poste).includes(normalizeString(posteS))
            );
        }
        if (dateDebut) {
            filteredData = filteredData.filter(emp =>
                new Date(emp.emp.dateEntre) >= new Date(dateDebut)
            );
        }
        if (dateAu) {
            filteredData = filteredData.filter(emp =>
                new Date(emp.emp.dateEntre) <= new Date(dateAu)
            );
        }

        setData(filteredData);
    };
    return(
        <>
            <div className="mx-0 mt-0 mb-3 p-0 w-100">
                <div className="mx-0 mt-0 mb-2 p-2 w-100 bg-white rounded-3 shadow">
                    <h3 className="text-center pt-2">Liste des Salariés</h3>
                    <hr className="w-50 mx-auto"/>
                    <div className="row m-0 p-0">
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">ID</InputGroup.Text>
                                <Form.Control
                                    placeholder="matricule"
                                    aria-label="matricule"
                                    aria-describedby="basic-addon1"
                                    type="search"
                                    value={idS}
                                    onChange={(event)=>{
                                        setId(event.target.value.replace(/\D/g, ''))
                                    }}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">Nom</InputGroup.Text>
                                <Form.Control
                                    placeholder="nom ou prénom"
                                    aria-label="nom"
                                    aria-describedby="nom"
                                    type="search"
                                    value={nomS}
                                    onChange={(event) => setNom(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-4">
                            <InputGroup className="my-3">
                                <InputGroup.Text id="basic-addon1">Poste</InputGroup.Text>
                                <Form.Control
                                    placeholder="poste"
                                    aria-label="Poste"
                                    aria-describedby="Poste"
                                    type="search"
                                    value={posteS}
                                    onChange={(event) => setPoste(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-6">
                            <InputGroup className="my-2">
                                <InputGroup.Text id="basic-addon1">Date d'entrée du</InputGroup.Text>
                                <Form.Control
                                    placeholder="Date d'entrée du"
                                    aria-label="Date d'entrée du"
                                    aria-describedby="Date d'entrée du"
                                    type="date"
                                    value={dateDebut}
                                    onChange={(event) => setDateDebut(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                        <div className="col-6">
                            <InputGroup className="my-2">
                                <InputGroup.Text id="basic-addon1">Date d'entrée au</InputGroup.Text>
                                <Form.Control
                                    placeholder="Date d'entrée au"
                                    aria-label="Date d'entrée au"
                                    aria-describedby="Date d'entrée au"
                                    type="date"
                                    value={dateAu}
                                    onChange={(event) => setDateAu(event.target.value)}
                                />
                            </InputGroup>
                        </div>
                    </div>
                </div>
                <div className="m-0 w-100 rounded-3 bg-white">
                    <div className="rounded-3 p-1 m-0 w-100">
                        <table className="table mb-0 table-striped table-hover">
                            <thead>
                                <tr className="text-center">
                                    <th scope="col">#</th>
                                    <th scope="col">Nom</th>
                                    <th scope="col">Prenom</th>
                                    <th scope="col">Tel</th>
                                    <th scope="col">Date d'entrée</th>
                                    <th scope="col">Poste</th>
                                    <th scope="col">Etat</th>
                                    {user.type.idType===1 ? (<th scope="col">Action</th>): ''}
                                </tr>
                            </thead>
                            <tbody>
                            {data.slice((active-1)*10,(active)*10).map((element,index)=>(
                                <tr key={index} >
                                    <th scope="row">{element.emp.employe.idEmp}</th>
                                    <td>{element.emp.employe.nom}</td>
                                    <td>{element.emp.employe.prenom}</td>
                                    <td>{element.emp.employe.tel}</td>
                                    <td>{element.emp.dateEntre}</td>
                                    <td>{element.emp.employe.type.poste}</td>
                                    <td><span className={`px-2 py-1 text-white rounded-3 d-flex justify-content-center fw-bold text-lowercase ${element.etat==='ACTIF' ? 'bg-success' : 'bg-warning'}`}>{element.etat}</span></td>
                                    {user.type.idType===1 ? (<td>
                                        <Dropdown>
                                            <Dropdown.Toggle
                                                id={`dropdown-${index}`}
                                                as={"button"}
                                                className="btn btn-sm border"
                                            >
                                                Option
                                            </Dropdown.Toggle>
                                            <Dropdown.Menu>
                                                <Dropdown.Item onClick={()=>{
                                                    const queryParams = new URLSearchParams({id: element.emp.employe.idEmp, name: element.emp.employe.nom, firstName: element.emp.employe.prenom});
                                                    navigate(`/employer/precence?${queryParams.toString()}`)
                                                }} >Voir Présence</Dropdown.Item>
                                                <Dropdown.Item>Solde de congé</Dropdown.Item>
                                                <Dropdown.Item>Something else</Dropdown.Item>
                                            </Dropdown.Menu>
                                        </Dropdown>
                                    </td>): ''}
                                </tr>
                            ))}
                            </tbody>
                        </table>
                    </div>
                    <div className="d-flex justify-content-end w-100 m-0 p-2">
                        <Pagination length={(data.length/10)+1} active={active} setActive={setActive}/>
                    </div>
                </div>
            </div>
        </>
    )
}
export default ListSalarier