import {APIURL} from "./APIURL";

export default class Allinfo {
    type
    nom
    prenom
    naissance
    tel
    adresse
    cin
    valide
    nationalite
    genre
    statut
    enfant
    niveau
    entre
    numCanps
    banque
    rib
    contact
    telContact
    login
    mdp
    idEmp
    constructor(type, nom, prenom, naissance, tel, adresse, cin, valide, nationalite, genre, statut, enfant, niveau, entre, numCanps, banque, rib, contact, telContact, login, mdp,idEmp) {
        this.type = type;
        this.nom = nom;
        this.prenom = prenom;
        this.naissance = new Date(naissance);
        this.tel = tel;
        this.adresse = adresse;
        this.cin = cin;
        this.valide = new Date(valide);
        this.nationalite = nationalite;
        this.genre = genre;
        this.statut = statut;
        this.enfant = enfant;
        this.niveau = niveau;
        this.entre = new Date(entre);
        this.numCanps = numCanps;
        this.banque = banque;
        this.rib = rib;
        this.contact = contact;
        this.telContact = telContact;
        this.login = login;
        this.mdp = mdp;
        this.idEmp = idEmp;
    }
    static async addEmployee(AllInfo) {
        const response = await fetch(APIURL() + "employe/" , {
            method: 'post',
            headers: {
                "Content-Type": 'application/json'
            },
            body: JSON.stringify(AllInfo)
        });

        if (response.status===400){
            throw new Error("Changer le Email/Nom")
        } else if (response.ok) {
            return await response.text();
        }else{
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }

    static async getEmp(){
        const response = await fetch(APIURL() + "infoEmp/getById" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json',
                "Authorization": 'Bearer '+ await window.electronStore.get('token'),
            },
        });

        if (response.ok) {
            return await response.json()
        }else{
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
    static remplaceSlash(str) {
        return str.replace(/\//g, '-+');
    };
    static async modifEmployee(allInfo,prevMdp) {
        const response = await fetch(APIURL() + "infoEmp/employe/"+Allinfo.remplaceSlash(prevMdp) , {
            method: 'post',
            headers: {
                "Content-Type": 'application/json'
            },
            body: JSON.stringify(allInfo)
        });

        if (response.status===400){
            throw new Error(await response.text())
        } else if (response.ok) {
            return await response.text();
        }else{
            console.log("statut : "+response.status)
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
    }
}