import React, {useCallback, useEffect, useState} from "react";
import SlideBar from "../Component/Slide/SlideBar";
import {Outlet, useNavigate} from "react-router-dom";
import Header from "../Component/Main/Header";
import {UserProvider} from "../Component/Main/Utilitaire/UserProvider";

const MemoizedSlideBar = React.memo(SlideBar);
const MemoizedHeader = React.memo(Header);

const Home = () => {
    const [idActive,setActive]=useState(1)
    const navigate=useNavigate()
    useEffect(()=>{
        if (window.electronStore.get('token')===null || window.electronStore.get('token')===undefined){
            navigate("/")
        }
    },[])
    const setValue = useCallback( (value) => {
        setActive(value)
    },[])
    return(
        <>
            <div className="bg-opacity-25 content w-100 min-vh-100">
                <UserProvider>
                    <MemoizedSlideBar idActive={idActive} setActive={setValue} />
                    <div className="float-end position-relative w-rest pt-3 px-2 vh-100 d-grid" style={{ gridTemplateRows: 'auto auto 1fr' }}>
                        <MemoizedHeader idActive={idActive} />
                        <div className="m-t-4 w-100 rounded-3">
                            <Outlet />
                        </div>
                    </div>
                </UserProvider>
            </div>
        </>
    )
}
export default Home