import {APIURL} from "./APIURL";

export default class Type {
    idType
    poste

    constructor(idType, poste) {
        this.idType = idType;
        this.poste = poste;
    }

    static async getAllPostes() {
        const response = await fetch(APIURL() + "postes" , {
            method: 'get',
            headers: {
                "Content-Type": 'application/json'
            },
        });
        if (!response.ok) {
            throw new Error('On ne peut pas faire cette requête pour le moment!');
        }
        const data = await response.json();
        return data.map(item => new Type(item.idType, item.poste));
    }
}