import React, {Fragment, useContext, useEffect, useRef, useState} from "react";
import {
    Button,
    FloatingLabel,
    Form,
    Modal,
    Offcanvas,
    Overlay,
    Popover,
    PopoverBody, ProgressBar,
    Toast
} from "react-bootstrap";
import {Routes} from "../../Route/Route";
import {useNavigate} from "react-router-dom";
import LoginMD from "../../Model/LoginMD";
import Modif from "./Utilitaire/Modif";
import {UserContext} from "./Utilitaire/UserProvider";

const Header = React.memo(({idActive}) => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const [showMessage, setShowMessage] = useState(false);
    const handleCloseMessage = () => setShowMessage(false);
    const handleShowMessage = () => setShowMessage(true);

    const [showUser, setShowUser] = useState(false);
    const [target, setTarget] = useState(null);
    const ref = useRef(null);

    const handleClick = (event) => {
        setShowUser(!showUser);
        setTarget(event.target);
    };

    const [icon,setIcon]=useState('')
    const [labels,setLabels]=useState([])

    useEffect(()=>{
        setIcon(Routes[idActive-1].icon)
        setLabels(Routes[idActive-1].labels)
    },[idActive])
    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    };


    const [hasShadow, setHasShadow] = useState(false);

    const handleScroll = () => {
        if (window.scrollY > 0) {
            setHasShadow(true);
        } else {
            setHasShadow(false);
        }
    };
    const navigate=useNavigate()
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);
    const [erreur,setErreur]=useState(false)
    const [erreurText,setErreurText]=useState('')
    const logOut = () => {
        LoginMD.logout().then((res)=>{
            localStorage.clear()
            window.electronStore.clear()
            navigate("/")
        }).catch((e)=>{
            setErreur(true)
            setErreurText(e.message)
        })
    }

    const { user, setUser, setIsModif } = useContext(UserContext);
    const [showModif,setShowModif]=useState(false)

    const [showUpdate,setShowUpdate]=useState(false)
    const [status,setStatus]=useState(0)
    const [message,setMessage]=useState('')
    const handleCheckUpdate = () => {
        setShowUpdate(true)
        window.checkUpdate.update()
        setShowUser(false)
    }
    useEffect(() => {
        window.checkUpdate.onUpdateStatus((event, message) => {
           setMessage(message)
        });
        return () => {
            window.checkUpdate.removeStatusListener();
        };
    }, []);

    useEffect(() => {
        window.checkUpdate.statusUpdate((event, status) => {
            setStatus(status)
        });
        return () => {
            window.checkUpdate.removeStatusUpdateListener();
        };
    }, []);
    useEffect(()=>{
        if (status < 0) {
            const timer = setTimeout(() => {
                setShowUpdate(false);
            }, 3000);
            return () => clearTimeout(timer);
        }
    },[status])
    useEffect(()=>{
        if(showUser && showModif){
            setShowUser(false)
        }
    },[showModif])
    return(
        <>
            <div className={`p-2 top-2 start-2 ${hasShadow ? 'bg-grey text-white' : 'bg-white bg-opacity-50'} shadow-lg rounded-3 position-fixed z-3 fw-bolder small d-inline-flex justify-content-between w-rest-p align-content-stretch align-items-center`}>
                <ul className="list-group list-group-horizontal d-inline-flex align-items-center h-100 bg-transparent">
                    <li className="list-group-item border-0 pe-0 bg-transparent"> <i className={`fas px-1 ${icon}`}/></li>
                    <li className="list-group-item border-0 pe-0 bg-transparent">/</li>
                    {labels.map((string,index)=>(
                        index<(labels.length - 1) ? (
                            <Fragment key={`${index}-fragment`}>
                                <li key={index} onClick={scrollToTop} className="list-group-item border-0 pe-0 bg-transparent cursor-pointer text-light-emphasis">{string}</li>
                                <li key={`${index}-slash`} className="list-group-item border-0 pe-0 bg-transparent">/</li>
                            </Fragment>
                        ) : (
                            <li key={index} className="list-group-item border-0 pe-0 bg-transparent cursor-pointer text-uppercase fw-bolder">{string}</li>
                        )
                    ))}
                </ul>
                <div className="h-100 d-inline-flex align-items-center">
                    <FloatingLabel label="Rechercher...">
                        <Form.Control className="bg-transparent" type="search" placeholder="..." />
                    </FloatingLabel>
                    <span className="h-100 p-3 cursor-pointer position-relative">
                         <i onClick={handleClick} ref={ref} className={`far icon-30 fa-user-circle ${user===null ? '': 'text-concept'}`}/>
                          <Overlay show={showUser} target={target} placement="bottom" container={ref} containerPadding={20} rootClose={true} transition={false} onHide={()=>setShowUser(false)}>
                                <Popover  id="popover-basic" className="bg-transparent rounded-2 border-0">
                                    <PopoverBody className="p-0 m-0 width-max bg-transparent border border-1 rounded-3" as="div">
                                        <h5 className="p-3 pb-2 bg-light rounded-top-3 mb-0 fs-6 text-center">
                                            <span className="text-uppercase text-lowercase">
                                                 {user?.nom}
                                            </span>
                                            <p className="m-0 p-0 pt-1 text-center text-capitalize">{user?.prenom}</p>
                                        </h5>
                                        <ul className="list-group list-group-flush rounded-bottom-3 w-100 m-0">
                                            <li className="list-group-item fw-bolder link-primary-in">Faire un Pause</li>
                                            <li className="list-group-item fw-bolder link-primary-in" onClick={()=>setShowModif(true)}>Modifier mes Infos</li>
                                            <li className="list-group-item fw-bolder link-primary-in">Régles interne</li>
                                            <li className="list-group-item fw-bolder link-primary-in" onClick={()=>logOut()}>Déconnexion</li>
                                            <li className="list-group-item fw-bolder link-primary-in" onClick={handleCheckUpdate}>Mise à jour du logiciel</li>
                                        </ul>
                                    </PopoverBody >
                                </Popover>
                          </Overlay>
                    </span>
                    {/*<span className="h-100 p-3 ps-0 cursor-pointer position-relative" onClick={handleShowMessage}>*/}
                    {/*    <Badge className="position-absolute top-3 end-3" bg="danger">15+</Badge>*/}
                    {/*    <i className="far icon-30 fa-message"/>*/}
                    {/*</span>*/}
                    {/*<span className="h-100 p-3 ps-0 cursor-pointer position-relative" onClick={handleShow}>*/}
                    {/*    <Badge className="position-absolute top-3 end-3" bg="danger">50</Badge>*/}
                    {/*    <i className="far icon-30 fa-bell"/>*/}
                    {/*</span>*/}
                </div>
            </div>
            <Offcanvas show={show} onHide={handleClose} placement={'end'}>
                <Offcanvas.Header closeButton>
                    <Offcanvas.Title>Notification</Offcanvas.Title>
                </Offcanvas.Header>
                <Offcanvas.Body>
                    Some text as placeholder. In real life you can have the elements you
                    have chosen. Like, text, images, lists, etc.
                </Offcanvas.Body>
            </Offcanvas>
            <Modal
                show={showMessage}
                onHide={handleCloseMessage}
                keyboard={false}
                size={"lg"}
                backdropClassName="width-max"
                dialogClassName="position-absolute end-4 bottom-0 rounded-3"
                contentClassName="bg-light vh-50"
            >
                <Modal.Header closeButton>
                    <Modal.Title>Message</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    I will not close if you click outside me. Do not even try to press
                    escape key.
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseMessage}>
                        Close
                    </Button>
                    <Button variant="primary">Understood</Button>
                </Modal.Footer>
            </Modal>

            <Modal
                show={showUpdate}
                onHide={()=>setShowUpdate(false)}
                keyboard={false}
                size={"lg"}
                // backdropClassName="width-max"
                // dialogClassName="position-absolute end-4 bottom-0 rounded-3"
                // contentClassName="bg-light vh-50"
            >
                <Modal.Header closeButton>
                    <Modal.Title>Mise à jour du logiciel</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {status===2 ? (
                        <ProgressBar now={message} label={`${message} %`} />
                    ): (message)}
                </Modal.Body>
            </Modal>

            <Modif show={showModif} close={()=>setShowModif(false)}
                   object={user} changeValueUser={setUser} isModifier={setIsModif}/>
            <div className="position-fixed bottom-0 end-0 width-max p-2">
                <Toast bg={"danger"} show={erreur} onClose={()=>setErreur(false)} autohide delay={5000}>
                    <Toast.Header>
                        <i className="rounded me-2 text-aris icon-30 fas fa-circle-exclamation"/>
                        <strong className="me-auto">Erreur</strong>
                        <small className="text-muted">Maintenant</small>
                    </Toast.Header>
                    <Toast.Body className><strong>{erreurText}</strong></Toast.Body>
                </Toast>
            </div>
        </>
    )
})

export default Header