import React, {useContext, useEffect, useMemo, useRef, useState} from "react";
import {
    Badge,
    Button,
    Card,
    CardBody,
    CardTitle,
    Col,
    FloatingLabel,
    Form,
    Modal,
    Offcanvas, Overlay, Popover, PopoverBody,
    Row
} from "react-bootstrap";
import Pointage from "../Model/Pointage";
import Conge from "../Model/Conge";
import {UserContext} from "./Main/Utilitaire/UserProvider";

const Content = () => {
    const {user: userContext}=useContext(UserContext)
    const [pointage,setPointage]=useState(null)
    const [solde,setSolde]=useState(0)
    useEffect(()=>{
        if (userContext){
            Pointage.getPointageNow().then((res)=>{
                setPointage(res)
            })
            Conge.getSold(userContext?.idEmp).then((res)=>{
                setSolde(parseFloat(res))
            })
        }
    },[userContext])
    const getHeureMin = (date) => {
        if (!date) return "--:--";
        const d = new Date(date);
        const hours = String(d.getHours()).padStart(2, '0');
        const minutes = String(d.getMinutes()).padStart(2, '0');
        return `${hours}:${minutes}`;
    };
    return(
        <>
            <div className="w-100 rounded-3">
                <Row className="m-0 p-0 w-100">
                    <Col md={4} className="ps-0">
                        <Card className="bg-green">
                            <CardTitle className="text-center text-white pt-1 mb-0">Pointage</CardTitle>
                            <CardBody className="mt-0 text-center d-inline-flex justify-content-between">
                                <pre className="p-0 m-0">{getHeureMin(pointage?.dateDebut)}</pre><i className="fas fa-arrow-right"/><pre className="p-0 m-0">{getHeureMin(pointage?.dateFin)}</pre>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col md={4}>
                        <Card className="bg-blue-darker">
                        {/*<Card className="bg-white">*/}
                            <CardTitle className="text-center pt-1 mb-0 text-white">Prime</CardTitle>
                            <CardBody className="mt-0 text-center d-inline-flex justify-content-between">
                                <pre className="p-0 m-0">0,0</pre><span>MGA</span>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col md={4} className="pe-0">
                        <Card className="bg-red">
                            <CardTitle className="text-center pt-1 mb-0 text-white">Solde Congés</CardTitle>
                            <CardBody className="mt-0 text-center d-inline-flex justify-content-between">
                                <pre className="p-0 m-0">{solde}</pre><span>Jour(s)</span>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </div>
            <div className="mt-4 w-100 rounded-3 pb-3">
                <Card className="h-100 bg-green">
                    <CardTitle className="text-center pt-1 mb-0"> Aris Actu</CardTitle>
                    <CardBody className="mt-0 text-center">

                    </CardBody>
                </Card>
            </div>
        </>
    )
}

export default Content