import React, {useEffect, useState} from "react";
import {Col, Form, Row} from "react-bootstrap";
import Allinfo from "../../../Model/Allinfo";

const ModifRh = ({setActif,object,changeValueUser,isModifier, close}) => {

    const [entre,setEntre]=useState(new Date())
    const [numCanps,setCnaps]=useState('')
    const [banque,setBanque]=useState(null)
    const [rib,setRib]=useState('')
    const [contact,setContact]=useState('')
    const [telContact,setTelContact]=useState('')
    useEffect(()=>{
        if (object){
            setEntre(new Date(object.entre))
            setCnaps(object.numCanps)
            setBanque(object.banque)
            setRib(object.rib)
            setContact(object.contact)
            setTelContact(object.telContact)
        }
    },[object])
    const [allBanque,]=useState([
        {id: 1, value: 'BOA Madagascar'},
        {id: 2, value: 'BNI Madagascar'},
        {id: 3, value: 'Société générale Madagasikara (BFV - SG)'},
        {id: 4, value: 'BMOI'},
        {id: 5, value: 'AccèsBanque Madagascar'},
    ])

    const [validated, setValidated] = useState(false);
    const formatDateToInputValue = (date) => {
        const year = String(date.getFullYear()).padStart(4, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };
    const handleInputChange = (e, setValue) => {
        let value = e.target.value;
        let parts = value.split('-');

        if (parts.length === 3) {
            parts[0] = parts[0] === '' ? '0000' : parts[0].padStart(4, '0');
            parts[1] = parts[1] === '' ? '01' : parts[1].padStart(2, '0');
            parts[2] = parts[2] === '' ? '01' : parts[2].padStart(2, '0');
        }
        const formattedValue = parts.join('-');
        setValue(new Date(formattedValue));
    };
    const changeValue = () => {
        changeValueUser({
            ...object,
            entre:entre,
            numCanps:numCanps,
            banque:banque,
            rib:rib,
            contact:contact,
            telContact:telContact
        })
    }
    const [navigate,setNavigate]=useState(0)
    const handleSubmit = (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        }else{
            if (navigate===1){
                Allinfo.modifEmployee({...object,
                    entre:entre,
                    numCanps:numCanps,
                    banque:banque,
                    rib:rib,
                    contact:contact,
                    telContact:telContact
                },object.mdp).finally(()=>{
                    close()
                    isModifier((prev)=>!prev)
                })
            }else if (navigate===2){
                setActif(3)
                changeValue()
            }else{
                return
            }
        }
        setValidated(true);
    };

    return (
        <>
            <Form noValidate validated={validated} autoComplete={"off"} onSubmit={handleSubmit}>
                <Row className="mb-3 border-bottom">
                    <Form.Group className="mb-3" as={Col} md="6" controlId="entre">
                        <Form.Label className="fw-bold text-light-emphasis">Date d'entrée</Form.Label>
                        <Form.Control
                            required
                            type="date"
                            className="bg-transparent border-dark-light"
                            onChange={(e)=>handleInputChange(e,setEntre)}
                            value={formatDateToInputValue(entre)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="cnaps">
                        <Form.Label className="fw-bold text-light-emphasis">Numeros CNAPS</Form.Label>
                        <Form.Control
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="2321 1056 456"
                            value={numCanps}
                            onChange={(e)=>{
                                let value=e.target.value
                                value = value.replace(/\D/g, '')
                                setCnaps(value.replace(/(.{4})/g, '$1 ').trim())
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="banque">
                        <Form.Label className="fw-bold text-light-emphasis">Banque</Form.Label>
                        <Form.Select value={banque?.id} onChange={(html)=>setBanque(allBanque.find((element)=>element.id===parseInt(html.target.value,10)))} className="bg-transparent border-dark-light" required>
                            {allBanque.map((e)=>(
                                <option key={e.id} className="text-dark" value={e.id}>{e.value}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="rib">
                        <Form.Label className="fw-bold text-light-emphasis">Numeros RIB</Form.Label>
                        <Form.Control
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="5646 5112 12"
                            value={rib}
                            onChange={(e)=>{
                                let value=e.target.value
                                value = value.replace(/\D/g, '')
                                setRib(value.replace(/(.{4})/g, '$1 ').trim())
                            }}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="nom">
                        <Form.Label className="fw-bold text-light-emphasis">Nom (Contact en cas d'urgence)</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="Rakoto"
                            value={contact}
                            onChange={(e)=>setContact(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" as={Col} md="6" controlId="tel">
                        <Form.Label className="fw-bold text-light-emphasis">Tél (Contact en cas d'urgence)</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            className="bg-transparent border-dark-light"
                            placeholder="03xxxxxx - 03xxxxxxx"
                            value={telContact}
                            onChange={(e)=>{
                                let string=e.target.value
                                string= string.replace(/-/g, ' - ')
                                string= string.replace(/\s+/g, ' ').trim()
                                setTelContact(string)
                            }}
                        />
                    </Form.Group>
                </Row>
                <Row className="mb-2">
                    <Col md={4}>
                        <button type="button" onClick={()=>setActif(1)} className="btn btn-warning text-white w-100">Précedent</button>
                    </Col>
                    <Col md={4}>
                        <button type="submit" onClick={()=>setNavigate(1)} className="btn bg-blue text-white w-100">Terminé</button>
                    </Col>
                    <Col md={4}>
                        <button type="submit" onClick={()=>setNavigate(2)} className="btn btn-info text-white w-100">Suivant</button>
                    </Col>
                </Row>
            </Form>
        </>
    )
}

export default ModifRh