import React, {useState, useEffect, useMemo, useCallback} from "react";
import '../Presence/calendar.css';
import Pointage from "../../../Model/Pointage";
const printHeure=(day)=>{
    if (!day) return "--:--";
    const d = new Date(day);
    const hours = String(d.getHours()).padStart(2, '0');
    const minutes = String(d.getMinutes()).padStart(2, '0');
    return `${hours}:${minutes}`;
}
const calTotalHeur = (tabDateSemain)=>{
    let totalHeures = 0;
    tabDateSemain.forEach((element) => {
        if (element.dateDebut && element.dateFin) {
            const differenceMs = element.dateFin.getTime() - element.dateDebut.getTime();
            const differenceHeures = differenceMs / (1000 * 60 * 60);
            totalHeures += differenceHeures;
        }
    });

    return totalHeures.toFixed(2);
}
const PointageCalendar = React.memo(({linkUser, name}) => {
    const [currentDate] = useState(new Date());
    const [year, setYear] = useState(currentDate.getFullYear());
    const [month, setMonth] = useState(currentDate.getMonth());
    const [dayClicked,setDayClicked]=useState(currentDate.getDate())

    const monthYear = useMemo(()=>["Jan", "Fev", "Mar", "Avr", "Mai", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],[])
    const daysInWeek = useMemo(()=>['Dim', 'Lun', 'Mar', 'Mer', 'Jeu', 'Ven', 'Sam'],[])

    const daysInMonth = useMemo(() => new Date(year, month + 1, 0).getDate(), [year, month]);
    const firstDayOfMonth = useMemo(() => new Date(year, month, 1).getDay(), [year, month]);

    const dates = useMemo(() => {
        const initialDates = Array(firstDayOfMonth).fill(null).concat(
            Array.from({ length: daysInMonth }, (_, i) => i + 1)
        );
        while (initialDates.length % 7 !== 0) {
            initialDates.push(null);
        }
        return initialDates;
    }, [daysInMonth, firstDayOfMonth]);

    const today = useMemo(() => {
        return (currentDate.getFullYear() === year && currentDate.getMonth() === month)
            ? currentDate.getDate()
            : null;
    }, [currentDate, year, month]);
    const[presents,setPresents]=useState([])
    useEffect(() => {
        if (linkUser){
            Pointage.getPointagePerMonth(linkUser,month+1,year).then((res)=> {
                const tempPresents = [];
                dates.forEach((number) => {
                    if (number) {
                        const pointage = res.find((p) => new Date(p.dateDebut).getDate() === number);
                        tempPresents.push({
                            id: number,
                            isPresent: !!pointage,
                            dashed: pointage ? pointage.dateFin === null : true,
                            dateDebut: pointage ? new Date(pointage.dateDebut) : null,
                            dateFin: pointage && pointage.dateFin ? new Date(pointage.dateFin) : null,
                        });
                    } else {
                        tempPresents.push({
                            id: null,
                            isPresent: null,
                            dashed: null,
                            dateDebut: null,
                            dateFin: null,
                        });
                    }
                })
                setPresents(tempPresents);
            })
        }
    }, [year, month,linkUser]);

    const handleDayClick = useCallback((day) => {
        setDayClicked(day)
    }, []);

    const [dayActive,setDayActive]=useState(null)

    useEffect(()=>{
        if (dayClicked){
            setDayActive(()=>{
                return presents.find((element)=>element.id===dayClicked)
            })
        }
    },[dayClicked,presents])

    const yearClicked =useCallback((value) => {
        setYear(prev => prev +value)
    },[])
    return (
        <>
            <div className="card mb-3">
                <div className="card-body">
                    <h3 className="text-center pb-1">Pointage de : <span className="m-0 p-0 small fs-5 text-capitalize">{name}</span></h3>
                    <hr className="w-50 mx-auto pb-2"/>
                    <div className="row m-0 p-0 mb-1">
                        <div className="col-8 ps-0">
                            <div className="p-2 border rounded-3">
                                <div className="centered-year-container my-2">
                                    <div>
                                        <i onClick={() =>yearClicked(-1)} className="fas fa-chevron-left fs-5 cursor-pointer"/>
                                    </div>
                                    <div className="year">
                                        <pre className="text-dark fs-5 m-0 p-0">{year}</pre>
                                    </div>
                                    <div>
                                        <i onClick={() => yearClicked(+1)} className="fa fa-chevron-right fs-5 cursor-pointer"/>
                                    </div>
                                </div>
                                <div className="month-content my-2">
                                    <table className="w-100 small text-center">
                                        <tbody>
                                        <tr className="months-row">
                                            {monthYear.map((value, index) => (
                                                <td
                                                    onClick={() => setMonth(index)}
                                                    className={`month cursor-pointer ${index === month ? 'fw-bolder' : ''}`}
                                                    key={index}
                                                >
                                                    {value}
                                                </td>
                                            ))}
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div className="day-content border-bottom pb-2">
                                    <table className="days-table w-100">
                                        <thead>
                                        <tr>
                                            {daysInWeek.map((day, index) => (
                                                <th key={index} className="day p-0">{day}</th>
                                            ))}
                                            <th className="day p-0"/>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {Array.from({ length: presents.length / 7 }, (_, weekIndex) => (
                                            <tr key={weekIndex}>
                                                {presents.slice(weekIndex * 7, (weekIndex + 1) * 7).map(({id,isPresent, dashed}, index) => {
                                                    const isWeekend = (index === 0 || index === 6);
                                                    const isToday = id === today;
                                                    return (
                                                        <td key={index}
                                                            className={`date-cell ${id ? 'cursor-pointer' : 'empty-cell'}`}
                                                        >
                                                            {id ? (
                                                                <div onClick={()=>handleDayClick(id)} className={`date-content shadow ${isWeekend ? 'border-week-end' : isToday ? 'border-now' : !isPresent ? 'border-absent' : dashed ? 'border-is-null' : 'border-active'} ${id===dayClicked ? 'text-aris fw-bold': ''}`}>
                                                                    <pre className="m-0 p-0">{id || ''}</pre>
                                                                </div>
                                                            ) : ('')}
                                                        </td>
                                                    );
                                                })}
                                                <td className="date-cell">
                                                    <pre className="m-0 p-1 bg-info-subtle rounded text-info fs-6 fw-bold">{calTotalHeur(presents.slice(weekIndex * 7, (weekIndex + 1) * 7))}</pre>
                                                </td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table>
                                </div>
                                <div className="row my-1 mx-0 p-0 w-100">
                                    <ul className="list-group list-group-horizontal justify-content-between">
                                        <li className="list-group-item border-0 bg-transparent d-inline-flex ms-0 ps-0">
                                            <div className="date-content shadow border-week-end"/>
                                            <div className="ps-2 my-auto">
                                                <pre className="m-0 p-0 fw-bolder">Week-end</pre>
                                            </div>
                                        </li>
                                        <li className="list-group-item border-0 bg-transparent d-inline-flex">
                                            <div className="date-content shadow border-now"/>
                                            <div className="ps-2 my-auto">
                                                <pre className="m-0 p-0 fw-bolder">Aujourd'hui</pre>
                                            </div>
                                        </li>
                                        <li className="list-group-item border-0 bg-transparent d-inline-flex">
                                            <div className="date-content shadow border-absent"/>
                                            <div className="ps-2 my-auto">
                                                <pre className="m-0 p-0 fw-bolder">Absent</pre>
                                            </div>
                                        </li>
                                        <li className="list-group-item border-0 bg-transparent d-inline-flex">
                                            <div className="date-content shadow border-active"/>
                                            <div className="ps-2 my-auto">
                                                <pre className="m-0 p-0 fw-bolder">Present</pre>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="col-4 border rounded-3">
                            <pre className="m-0 p-0 text-center fs-5 border-bottom pt-1 pb-2">{String(dayClicked).padStart(2,'0')}/{String(month+1).padStart(2,'0')}/{year}</pre>
                            <ul className="list-group list-group-flush w-100 h-100 d-flex justify-content-center">
                                <li className="list-group-item bg-transparent d-inline-flex border-0 justify-content-between">
                                    <div className="d-inline-flex">
                                        <div className="my-auto me-1">
                                            <i className="fa fa-door-open fs-4 text-success"/>
                                        </div>
                                        <div className="ms-1 my-auto">
                                            <pre className="m-0 p-0 fs-5 fw-bolder opacity-75 text-success">{printHeure(dayActive?.dateDebut)}</pre>
                                        </div>
                                    </div>
                                    <div className="d-inline-flex">
                                        <div className="my-auto me-1">
                                            <i className="fa fa-door-closed fs-4 text-aris"/>
                                        </div>
                                        <div className="ms-1 my-auto">
                                            <pre className="m-0 p-0 fs-5 fw-bolder opacity-75 text-aris">{printHeure(dayActive?.dateFin)}</pre>
                                        </div>
                                    </div>
                                </li>
                                <li className="list-group-item bg-transparent border-0 mt-2">
                                    <pre className="m-0 p-0 fs-5 fw-bolder"><span className="text-decoration-underline">Total Pause:</span> <span className="px-2 py-1 bg-info-subtle text-info rounded-3">0.0h</span></pre>
                                </li>
                                <li className="list-group-item bg-transparent border-0 mt-2">
                                    <pre className="m-0 p-0 fs-5 fw-bolder"><span className="text-decoration-underline">Total Actif:</span> <span className="px-2 py-1 bg-info-subtle text-info rounded-3">8.0h</span></pre>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
});

export default PointageCalendar;
